<template>
<div class="content">
  <p v-if="loading">loading...</p>
  <p v-if="error">{{ error }}</p>
  <ul v-for="item in topics" :key="item.id" class="content-ul">
    <Li class="list-item"><router-link v-bind:to="`/topic/${item.id}`">{{ item.title }}</router-link></Li>
  </ul>
</div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      loading: false,
      error: null
    }
  },
  name: 'content',
  created: function () {
    const url = 'https://cnodejs.org/api/v1/topics?page=1'
    this.fetchdata(url)
  },
  computed: {
    topics () {
      return this.$store.state.all
    }
  },
  methods: {
    ...mapActions([
      'fetchdata'
    ])
  }
}
</script>

<style scoped>
.content-ul {
  list-style: none;
}
.content-ul li {
  height: 50px;
  line-height: 50px;
  vertical-align: middle;
  text-align: left;
  border-top: 1px solid #eee;
  overflow: hidden;
}
</style>



