<template>
  <div class="login">
    <mu-text-field label="token" hintText="输入token" v-model="token" />
    <mu-raised-button label="登陆" v-on:click="submit" />
    <p style="padding-left: 50px">f4534249-4043-4f78-95a3-1c8bb18af29c</p>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      token: null,
      data: null
    }
  },
  methods: {
    submit () {
      const url = `https://cnodejs.org/api/v1/accesstoken?accesstoken=${this.token}`
      axios.post(url)
      .then(res => {
        let {id, loginname, avatar_url} = res.data
        let token = this.token
        let user = {id, loginname, avatar_url, token}
        this.data = user
        console.log(user)
        this.$store.dispatch('getUserInfo', user)
        // 函数是路由this.$router.puash()等于点击 <router-link to="">
        this.$router.push('/good')
        window.sessionStorage.user = JSON.stringify(user)
      })
      .catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.login {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
}

input {
  display: block;
  width: 100;
  margin: 0 auto;
  padding-top: 10px;
}
</style>


