<template>
<div>
  <li class="item" v-for="item in posts" :key="item.id">
    <div class="item-title">
      <!-- <span class="item-label">{{ getLabel(item) }}</span> -->
      <router-link  v-bind:to="`/topic/${item.id}`">
        {{ item.title }}
      </router-link>
    </div>
    <div class="item-author">
      <router-link v-bind:to="`/user/${item.author.loginname}`">
        <div class="chip">
          <img class="item-img" v-bind:src="item.author.avatar_url" v-bind:title="item.author.loginname" />
           {{ item.author.loginname }}
        </div>
      </router-link>
      
      <span class="item-count">{{ item.reply_count }}回复</span>
      
      <span class="item-last-reply">{{ timeAgo(item.last_reply_at) }}</span>
    </div>
  </li>
</div>
</template>

<script>
import {timeAgo} from '../uitls/index.js'
export default {
  props: ['posts'],
  methods: {
    getLabel: function (val) {
      if (val.top) {
        return '置顶'
      }
      if (val.good) {
        return '精华'
      }
      switch (val.tab) {
        case 'share':
          return '分享'
        case 'good':
          return '精华'
        case 'ask':
          return '问答'
        case 'job':
          return '招聘'
        case 'top':
          return '置顶'
        case 'dev':
          return '测试'
      }
    },
    timeAgo: function (val) {
      return timeAgo(val)
    }
  }
}
</script>

<style scoped >
.item {
  border-top: 1px solid #eee;
  overflow: hidden;
  padding: 10px;
}
/*.item-title {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
}*/
.item-author {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  /*height: 50px;
  line-height: 50px;
  vertical-align: middle;
  text-align: left;*/
}
.item-img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

.item-label {
  flex-grow: 1;
  padding: 2px;
  border: 1px solid blue;
}
a {
  margin-left: 50px;
  display: block;
  text-decoration: none;
  word-break: break-all;
  word-wrap: break-word;
  margin-left: 10px;
  flex-grow: 10;
  color: #000;
}
a:visited {
  color: #999;
}
a:hover,
a:active {
  text-decoration: underline;
}

.item-count {
  display: inline-block;
  min-width: 60px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 12px;
}

.item-last-reply {
  float: right;
  margin-right: 50px;
  font-size: 12px;
}
</style>


