<template>
<div class="post-details">
  <!-- see: http://loading.awesomes.cn/ -->
  <div v-if="isLoading" class="line-scale-pulse-out">
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
  </div>
  <div v-else>
    <h3>{{ post.title }}</h3>
    <div class="author">
      <img :src="post.author.avatar_url" alt="头像">&nbsp;
      <span>{{ post.author.loginname }}</span>&nbsp;
      <span>发布于{{ timeAgo(post.create_at) }}</span>&nbsp;
      <span>{{ post.visit_count }}次浏览</span>&nbsp;
      <!-- <button>{{ post.tab }}</button> -->
    </div>
    <p v-html="post.content"></p>

    <Comment :replies.sync="post.replies" />

    <p class="reply-count">{{ post.reply_count }}回复</p>
    <ul class="reply">
      <ReplyList v-bind:replies="post.replies" />
    </ul>
  </div>
</div>
</template>

<script>
import { mapActions } from 'vuex'
import ReplyList from './ReplyList'
import Comment from './Comment'
import { timeAgo } from '../uitls/index.js'
export default {
  name: 'postDetails',
  data () {
    return {
      isLoading: false,
      post: {
        author: {
          avatar_url: '',
          loginname: ''
        },
        id: '',
        content: '',
        create_at: '',
        replies: [],
        title: ''
      }
    }
  },
  // 还有一点疑问：这个PostDetails是一个动态路由共用组件啊，周期函数只执行一次，为什么这个每次都执行？
  created: function () {
    var url = `https://cnodejs.org/api/v1/topic/${this.$route.params.id}`
    // 这是异步执行
    // 是Promise解决了这个问题，加深了对异步的理解，Promise实现异步像同步一样去使用
    this.isLoading = true
    this.fetchpost(url)
    .then(() => {
      this.isLoading = false
      this.post = this.$store.state.post
    })
  },
  methods: {
    ...mapActions([
      'fetchpost'
    ]),
    timeAgo: function (val) {
      return timeAgo(val)
    }
  },
  components: {
    ReplyList,
    Comment
  }
}
</script>


<style scoped>
.post-details {
  text-align: left;
  padding: 30px;
  padding-top: 50px;
}

.author {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.author>img {
  width: 30px;
  height: 30px;
}

.reply {
  list-style: none;
  margin: 0;
  padding: 0;
}
.reply-count {
  border-bottom: 1px solid #999;
}

.line-scale-pulse-out {
  display: flex;
  height: 100vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;


  transform: scale(1);
}
.line-scale-pulse-out > div {
  /*align-self: center;*/
  background-color: #11D4C5;
  width: 4px;
  height: 35px;
  border-radius: 2px;
  margin: 2px;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  display: inline-block;
  -webkit-animation: line-scale-pulse-out 0.9s -0.60s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
  animation: line-scale-pulse-out 0.9s -0.60s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
}
.line-scale-pulse-out > div:nth-child(2), .line-scale-pulse-out > div:nth-child(4) {
  -webkit-animation-delay: -0.4s !important;
  animation-delay: -0.4s !important;
}
.line-scale-pulse-out > div:nth-child(1), .line-scale-pulse-out > div:nth-child(5) {
  -webkit-animation-delay: -0.2s !important;
  animation-delay: -0.2s !important;
}

@-webkit-keyframes line-scale-pulse-out {
  0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  }
  @keyframes line-scale-pulse-out {
    0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
}

</style>



