<template>
<div class="content-good">
  <ul class="content-ul" ref="reference">
    <ListItem v-bind:posts="posts" />
  </ul>
  <div v-if="isLoading" class="line-scale-pulse-out">
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
  </div>
  <p v-if="error">{{ error }}</p>
</div>
</template>

<script>
import axios from 'axios'
import ListItem from './ListItem'

export default {
  name: 'contentGood',
  data () {
    return {
      isLoading: false,
      error: null,
      page: 1,
      topic: null,
      canRun: true,
      posts: []
    }
  },
  mounted: function () {
    this.init()
    window.addEventListener('scroll', this.scrollProcessor)
  },
  destroyed: function () {
    window.removeEventListener('scroll', this.scrollProcessor)
  },
  // 利用导航守卫通过sessionStorage缓存数据解决回退不能返回详情页之前的滚动位置
  beforeRouteLeave (to, from, next) {
    if (to.name === 'postDetails') {
      window.sessionStorage.posts = JSON.stringify(this.posts)
      window.sessionStorage.page = JSON.stringify(this.page)
    }
    next()
  },
  methods: {
    init: function () {
      this.posts = []
      this.page = 1

      const topic = this.topic || this.$route.params.id
      this.topic = this.$route.params.id
      const page = this.page
      const url = `https://cnodejs.org/api/v1/topics?tab=${topic}&page=${page}`
      const posts = window.sessionStorage.posts
      // 为什么posts里面默认长度为2？
      if (posts && posts.length > 2) {
        this.posts = JSON.parse(window.sessionStorage.posts)
        this.page = JSON.parse(window.sessionStorage.page)
        window.sessionStorage.removeItem('posts')
        window.sessionStorage.removeItem('page')
      } else {
        this.fetchData(url)
        .then(res => {
          this.posts = this.posts.concat(...res.data.data)
        })
      }
    },
    fetchData: function (url) {
      return new Promise((resolve, reject) => {
        this.error = null
        this.isLoading = true
        axios.get(url)
        .then(res => {
          this.isLoading = false
          resolve(res)
        })
        .catch(err => {
          this.error = err.toString()
          reject(err)
        })
      })
    },
    // 滚动监听具体设置逻辑
    scrollProcessor () {
      var h = document.documentElement.scrollHeight - document.documentElement.scrollTop - document.documentElement.clientHeight
      console.log(h)
      if (h < 700) {
        if (!this.canRun) {
          return
        }
        // 函数节流优化触发次数
        this.canRun = false
        this.page += 1
        var self = this
        setTimeout(function () {
          const topic = self.$route.path.slice(1)
          const page = self.page
          const url = `https://cnodejs.org/api/v1/topics?tab=${topic}&page=${page}`
          self.fetchData(url)
          .then(res => {
            self.posts = self.posts.concat(...res.data.data)
          })
          self.canRun = true
        }, 1000)
      }
    }
  },
  // watch解决切换主题，组件不重新渲染问题（因为时动态路由渲染同一个组件）
  // see https://router.vuejs.org/zh-cn/essentials/dynamic-matching.html
  watch: {
    '$route': function (to, from) {
      if (to.name === 'goodContent' && from.name === 'goodContent') {
        this.topic = this.$route.params.id
        this.init()
      }
    }
  },
  components: {
    ListItem
  }
}
</script>


<style scoped>
.content-ul {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-top: 50px;
}

.line-scale-pulse-out {
  display: flex;
  height: 100vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;


  transform: scale(1);
}
.line-scale-pulse-out > div {
  /*align-self: center;*/
  background-color: #11D4C5;
  width: 4px;
  height: 35px;
  border-radius: 2px;
  margin: 2px;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  display: inline-block;
  -webkit-animation: line-scale-pulse-out 0.9s -0.60s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
  animation: line-scale-pulse-out 0.9s -0.60s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
}
.line-scale-pulse-out > div:nth-child(2), .line-scale-pulse-out > div:nth-child(4) {
  -webkit-animation-delay: -0.4s !important;
  animation-delay: -0.4s !important;
}
.line-scale-pulse-out > div:nth-child(1), .line-scale-pulse-out > div:nth-child(5) {
  -webkit-animation-delay: -0.2s !important;
  animation-delay: -0.2s !important;
}

@-webkit-keyframes line-scale-pulse-out {
  0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  }
  @keyframes line-scale-pulse-out {
    0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
}
</style>



