<template>
  <div>
    <li class="reply-item" v-for="(item,index) in repliesReverse" :key="item.id">
      <div class="author">
        <img :src="item.author.avatar_url" alt="头像">
        <span>{{ item.author.loginname }}</span>
        <span>发布于{{ timeAgo(item.create_at) }}</span>
      </div>
      <p v-html="item.content"></p>
      <!-- 点赞 -->
      <span class="good">
        <i class="material-icons orange600" v-bind:class="{active: isUps(item.ups)}" v-on:click="upReply(item)" >thumb_up</i>
        <span>{{ item.ups.length }}</span>
      </span>
    </li>
  </div>
</template>

<script>
import axios from 'axios'
import { timeAgo } from '../uitls/index.js'
export default {
  name: 'comment',
  props: ['replies'],
  data () {
    return {
      repliesReverse: this.replies.reverse()
    }
  },
  // 自动计算获取登陆用户信息，提供后面登陆验证支持
  // 待优化： 1 重定向到登陆页面提示，登陆成功重定向返回评论位置（页面和滚动位置)
  computed: {
    userInfo: function () {
      // 先查看本地缓存是否有
      let userInfo = JSON.parse(window.sessionStorage.getItem('user'))
      if (userInfo) {
        return userInfo
      } else {
        return this.$store.state.userInfo
      }
    }
  },
  methods: {
    isUps: function (ups) {
      // es6语法 判断数组中书否包含登陆用户id
      return ups.includes(this.userInfo.id)
    },
    upReply (item) {
      // 检测用户是否登陆
      if (!this.userInfo.id) {
        this.$router.push({
          name: 'login',
          params: {
            redirect: encodeURIComponent(this.$route.path)
          }
        })
      } else {
        let url = `https://cnodejs.org/api/v1/reply/${item.id}/ups?accesstoken=${this.userInfo.token}`
        axios.post(url)
        .then(res => {
          console.log(res.data)
          if (res.data.action === 'down') {
            // 在ups数组中删除用户id元素
            let index = item.ups.indexOf(this.userInfo.id)
            item.ups.splice(index, 1)
          } else {
            item.ups.push(this.userInfo.id)
          }
        })
        .catch(err => {
          console.log(err.toString())
          return false
        })
      }
    },
    timeAgo: function (val) {
      return timeAgo(val)
    }
  }
}
</script>

<style scoped>
.author {
  display: flex;
  justify-content: left;
  align-items: center;
  /*padding: 2px;*/
}
img {
  width: 30px;
  height: 30px;
  border-radius: 10px;
}


.reply-item {
  border-bottom: 1px solid #999;
}

.good {
  float: right;
  margin-right: 20px;
}

.orange600 {
  color: #FB8C00; 
}

.active {
  color: blue;
}
</style>


