
'use strict'
export function timeAgo (val) {
  const t = (Date.now() - Date.parse(val)) / 1000
  if (t > 60) {
    if (t / 60 > 60) {
      if (t / 60 / 60 > 24) {
        if (t / 60 / 60 / 24 > 30) {
          if (t / 60 / 60 / 24 / 30 > 12) {
            return ~~(t / 60 / 60 / 24 / 30 / 12) + '年前'
          }
          return ~~(t / 60 / 60 / 24 / 30) + '月前'
        }
        return ~~(t / 60 / 60 / 24) + '天前'
      }
      return ~~(t / 60 / 60) + '小时前'
    }
    return ~~(t / 60) + '分钟前'
  }
  return ~~t + '秒前'
}



