<template>
	<div class="comment-box">
    <textarea class="comment" @focus="isLogin" v-model="comment" placeholder="写下你的评论..."></textarea>
    <button class="submit" @click="addReply">确定</button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'comment',
  props: ['replies'],
  data () {
    return {
      comment: ''
    }
  },
  computed: {
    userInfo: function () {
      let userInfo = JSON.parse(window.sessionStorage.user)
      if (userInfo) {
        return userInfo
      } else {
        return this.$store.state.userInfo
      }
    }
  },
  methods: {
    isLogin: function () {
      if (!this.userInfo.id) {
        this.$router.push({
          name: 'login'
        })
      } else {

      }
    },
    addReply: function () {
      let url = `https://cnodejs.org/api/v1/topic/${this.$route.params.id}/replies`
      axios.post(url, {
        content: this.comment,
        accesstoken: this.userInfo.token
      })
      .then(res => {
        console.log(res.data)
        let now = new Date()
        this.replies.push({
          author: {
            avatar_url: this.userInfo.avatar_url,
            loginname: this.userInfo.loginname
          },
          id: this.userInfo.id,
          content: this.comment,
          create_at: now,
          is_uped: false,
          reply_id: res.data.reply_id,
          ups: []
        })
      })
      .catch(err => {
        console.log(err.toString())
        return false
      })
    }
  }
}
</script>

<style>
.comment {
  display: block;
  width: 100%;
  height: 100px;
  outline: none;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.comment:focus {
  border: 1px solid #007fff;
  border-radius: 5px;
}

.submit {
  display: block;
  widows: 100%;
  height: 30px;
  line-height: 30px;
  margin-top: 10px;
  text-align: center;
  vertical-align: middle;
  outline: none;
  border: 1px solid green;
}
 
.submit:active {
  background: green;
}
</style>


