<template>
<div class="user-profile">
  <div v-if="isLoading" class="line-scale-pulse-out">
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
  </div>
  <p v-if="error">{{ error }}</p>

  <div v-else>
    <div class="img-box">
      <img class="img" v-bind:src="post.data.avatar_url">
      <h3>{{ post.data.loginname }}</h3>
    </div>
    <p>{{ post.data.score }}积分</p>

    <mu-tabs :value="activeTab" @change="handleTabChange">
      <mu-tab value="tab1" title="最近回复" />
      <mu-tab value="tab2" title="最新发布" />
      <mu-tab value="tab3" title="话题收藏" />
    </mu-tabs>
    <div v-if="activeTab === 'tab1'">
      <ul>
        <h3>最近参与的话题</h3>
        <ListItem v-bind:posts="post.data.recent_replies" />
      </ul>
    </div>
    <div v-if="activeTab === 'tab2'">
      <ul>
        <h4>最近创建的话题</h4>
        <ListItem v-bind:posts="post.data.recent_topics" />
      </ul>
    </div>

    <div v-if="activeTab === 'tab3'">
      <h2>tab three</h2>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import ListItem from './ListItem'

export default {
  data () {
    return {
      isLoading: false,
      error: false,
      activeTab: 'tab1',
      post: {
        data: {
          avatar_url: null,
          loginname: null,
          score: null,
          recent_replies: null,
          recent_topics: null
        }
      }
    }
  },
  mounted: function () {
    const url = `https://cnodejs.org/api/v1/user/${this.$route.params.id}`
    this.isLoading = true
    this.fetch(url)
    .then(res => {
      this.post = res.data
      this.isLoading = false
    })
  },
  methods: {
    handleTabChange: function (val) {
      this.activeTab = val
    },
    fetch: function (url) {
      return new Promise((resolve, reject) => {
        axios.get(url)
        .then(res => {
          console.log(res)
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    }
  },
  components: {
    ListItem
  }
}
</script>

<style>
.user-profile {
  padding-top: 50px;
}
.img-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
}

.line-scale-pulse-out {
  display: flex;
  height: 100vh;
  flex-direction: row;
  justify-content: center;
  align-items: center;


  transform: scale(1);
}
.line-scale-pulse-out > div {
  /*align-self: center;*/
  background-color: #11D4C5;
  width: 4px;
  height: 35px;
  border-radius: 2px;
  margin: 2px;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  display: inline-block;
  -webkit-animation: line-scale-pulse-out 0.9s -0.60s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
  animation: line-scale-pulse-out 0.9s -0.60s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
}
.line-scale-pulse-out > div:nth-child(2), .line-scale-pulse-out > div:nth-child(4) {
  -webkit-animation-delay: -0.4s !important;
  animation-delay: -0.4s !important;
}
.line-scale-pulse-out > div:nth-child(1), .line-scale-pulse-out > div:nth-child(5) {
  -webkit-animation-delay: -0.2s !important;
  animation-delay: -0.2s !important;
}

@-webkit-keyframes line-scale-pulse-out {
  0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  }
  @keyframes line-scale-pulse-out {
    0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
}
</style>


