<template>
	<div class="create-post">
    <h1>新建主题</h1>

    <div class="form">
      <div>
        <span>发布到分类</span>
        <select class="tab" v-model="tab">
          <option value="share">分享</option>
          <option value="dev">测试</option>
          <option value="good">精华</option>
          <option value="ask">问答</option>
        </select>
      </div>
      <div><input type="text" class="name" placeholder="请输入主题名" v-model="title" /></div>
      <textarea v-model="content" class="content" placeholder="主题内容..."></textarea>
      <div><button class="submit" @click="submit">确定</button></div>
    </div>
  </div>
</template>


<script>
import axios from 'axios'
export default {
  name: 'createPost',
  data () {
    return {
      tab: '',
      title: '',
      content: ''
    }
  },
  computed: {
    userInfo: function () {
      return this.$store.state.userInfo
    }
  },
  methods: {
    submit () {
      let url = 'https://cnodejs.org/api/v1/topics'
      axios.post(url, {
        tab: this.tab,
        title: this.title,
        content: this.content,
        accesstoken: this.userInfo.token
      })
      .then(res => {
        console.log(res.data)
      })
      .catch(err => {
        console.log(err.toString())
        return false
      })
    }
  }
}
</script>

<style scoped>
.create-post {
  padding-top: 50px;
}

.form {
  width: 400px;
  margin: 0 auto;
}
.tab,
.name,
.content,
.submit {
  display: block;
  width: 400px;
  height: 30px;
  margin-top: 20px;
  border: 1px solid #666;
  outline: none;
}

.content {
  height: 100px;
}

.tab:focus,
.name:focus,
.content:focus,
.submit:active {
  border: 1px solid blue;
}
</style>


