<template>
  <div id="app">
    <div id="menu" ref="menu">
      <!-- 子组件向父组件通信 -->
      <HeaderNav v-on:receiveChildEvent="childCallBack" />
    </div>
    <header class="fix-header" ref="header">
        <i v-on:click="toggleSlide" class="material-icons toggle-button">menu</i>
        <span v-text="topTitle" class="top-title"></span>
        <router-link to="/create"><i class="material-icons create">create</i></router-link>
    </header>
    <main class="panel" ref="panel">
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
import Slideout from 'slideout'
import HeaderNav from './components/HeaderNav'
export default {
  name: 'app',
  data () {
    return {
      navTitle: '',
      slideout: null,
      topTitle: ''
    }
  },
  mounted () {
    var slideout = new Slideout({
      'panel': this.$refs.panel,
      'menu': this.$refs.menu,
      'padding': 240,
      'tolerance': 70
    })
    var fixed = this.$refs.header
    function close (eve) {
      eve.preventDefault()
      slideout.close()
    }
    this.slideout = slideout
    slideout.on('translate', function (translated) {
      fixed.style.transform = 'translateX(' + translated + 'px)'
    })

    slideout.on('beforeopen', function () {
      fixed.style.transition = 'transform 300ms ease'
      fixed.style.transform = 'translateX(240px)'
      this.panel.classList.add('panel-open')
    })

    slideout.on('beforeclose', function () {
      fixed.style.transition = 'transform 300ms ease'
      fixed.style.transform = 'translateX(0px)'
      this.panel.classList.remove('panel-open')
      this.panel.removeEventListener('click', close)
    })

    slideout.on('open', function () {
      fixed.style.transition = ''
      this.panel.addEventListener('click', close)
    })

    slideout.on('close', function () {
      fixed.style.transition = ''
    })
  },
  methods: {
    toggleSlide () {
      this.slideout.toggle()
    },
    childCallBack: function (data) {
      this.topTitle = data
      this.toggleSlide()
    }
  },
  components: {
    HeaderNav
  }
}
</script>

<style> 
/*body {
    font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Tahoma,Simsun,sans-serif;

}*/
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }

  .panel {
    width: 100%;
    min-height: 100vh;
    background: #fff;
  }

  /* slideout open时内容区域添加覆盖层效果 */
  .panel:before {
    content: '';
    display: block;
    background: rgba(0,0,0,0);
    transition: background-color 0.5s ease-in-out;
  }
  
  .panel-open:before {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background: rgba(0,0,0,.5);
    z-index: 99;
  }

  .fix-header {
    position: fixed;
    width: 100%;
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
    backface-visibility: hidden;
    z-index: 2;
    background-color: red;
    margin: 0;
    padding: 0;
  }

  .slideout-menu {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 0;
    width: 240px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    display: none;
  }

  .slideout-panel {
    position: relative;
    z-index: 1;
    will-change: transform;
  }

  .slideout-open,
  .slideout-open body,
  .slideout-open .slideout-panel {
    overflow: hidden;
  }

  .slideout-open .slideout-menu {
    display: block;
  }

  .material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    cursor: default;
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;

    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;

    /* Support for IE. */
    font-feature-settings: 'liga';
  }
  .toggle-button,
  .create {
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
    padding-left: 5px;
  }

  .create {
    float: right;
    margin-right: 20px;
  }
  .top-title: {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
  }
</style>


