<template>
<ul class="header-nav" v-on:click="sendToParent">
  
  <router-link v-if="userInfo.loginname" v-bind:to="`/user/${userInfo.loginname}`" tag="li">
      <img class="user-img" :src="userInfo.avatar_url"/>
      <span class="user-name"> {{ userInfo.loginname }} </span>
  </router-link>

  <router-link v-else to="/login" tag="li">
    <i class="material-icons">perm_identity</i>
    <span>登陆</span>
  </router-link>

  <router-link to="/" tag="li">
    <i class="material-icons">home</i>
    <span>全部</span>
  </router-link>
  <router-link to="/good" tag="li">
    <i class="material-icons">thumb_up</i>
    <span>精华</span>
  </router-link>
  <router-link to="/share" tag="li">
    <i class="material-icons">share</i>
    <span>分享</span>
  </router-link>
  <router-link to="/ask" tag="li">
    <i class="material-icons">question_answer</i>
    <span>问答</span>
  </router-link>
  <router-link to="/job" tag="li">
    <i class="material-icons">group</i>
    <span>招聘</span>
  </router-link>
  <router-link to="/dev" tag="li">
    <i class="material-icons">build</i>
    <span>测试</span>
  </router-link>
  <router-link to="/syoogool/message" tag="li">
    <i class="material-icons">info</i>
    <span>消息</span>
  </router-link>
  <router-link to="/syoogool/about" tag="li">
    <i class="material-icons">account_box</i>
    <span>关于</span>
  </router-link>
</ul>
</template>

<script>
export default {
  name: 'header-nav',
  data () {
    return {
      user: null
    }
  },
  computed: {
    userInfo: function () {
      // 先查看本地缓存是否有
      let userInfo = JSON.parse(window.sessionStorage.getItem('user'))
      if (userInfo) {
        console.log(userInfo)
        return userInfo
      } else {
        return this.$store.state.userInfo
      }
    }
  },
  methods: {
    sendToParent: function () {
      this.$emit('receiveChildEvent', this.$route.params.id)
    }
  }
}
</script>


<style scoped>
  .header-nav {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .header-nav li {
    height: 40px;
    text-align: center;
  }
  .header-nav li:hover,
  .header-nav li:active {
    background: #ddd;
  }
  .router-link-active {
    color: green;
  }

  .material-icons,
  a {
    height: 40px;
    line-height: 40px;
    vertical-align: middle;
    margin-left: 40px;
  }
  .user-img,
  .user-name {
    width: 40px;
    height: 40px;
    vertical-align: middle;
  }

  .user-img {
    border-radius: 50%;
  }
</style>



